<template>
  <section class="">
    <h2 class="fw-bolder mb-3">{{ $t('drinkProducts.title') }}</h2>
    <div class="d-flex justify-content-center mb-3 imgSet">
      <template v-for="(item, index) in productList" :key="index">
        <img
          :src="item.img"
          class="img-fluid drinkList"
          alt=""
          style="width: 75%"
        />
      </template>
    </div>
    <div class="d-md-flex justify-content-between align-items-start mb-5">
      <ul class="h4">
        <li class="fw-bold mb-2">{{ $t('drinkProducts.classification') }}</li>
        <ul class="fw-lighter text-dark-emphasis ambienceSet">
          <template v-for="(item, index) in productList" :key="index">
            <li
              class="mb-1"
              v-if="item.type === 'tea'"
              :class="{ focusLine: item.id !== productId }"
            >
              <a
                href="#"
                class="d-inline fw-lighter text-dark-emphasis text-decoration-none"
                @click.prevent
                @mouseover="changProduct(index, item.id)"
                @focus="bar"
              >
                {{ $t(item.name) }}
              </a>
            </li>
          </template>
        </ul>
      </ul>
      <ul class="h4">
        <li class="fw-bold mb-2">{{ $t('drinkProducts.classification1') }}</li>
        <ul class="fw-lighter text-dark-emphasis ambienceSet">
          <template v-for="(item, index) in productList" :key="index">
            <li
              class="mb-1"
              v-if="item.type === 'coffee'"
              :class="{ focusLine: item.id !== productId }"
            >
              <a
                href="#"
                class="d-inline fw-lighter text-dark-emphasis text-decoration-none"
                @click.prevent
                @mouseover="changProduct(index, item.id)"
                @focus="bar"
              >
                {{ $t(item.name) }}
              </a>
            </li>
          </template>
        </ul>
      </ul>
    </div>
    <div class="text-end mt-auto">
      <small class="text-body-secondary">Full report on page 8</small>
    </div>
  </section>
</template>

<script>
import drink1 from '@/assets/images/drink.png';
import drink2 from '@/assets/images/bread-01.png';
import drink3 from '@/assets/images/bread-02.png';
import drink4 from '@/assets/images/bread-03.png';
import drink5 from '@/assets/images/bread-04.png';

export default {
  data() {
    return {
      productId: '001',
      productList: [
        {
          name: 'drinkProducts.drinkList[0].appleTea',
          type: 'tea',
          img: drink1,
          id: '001',
        },
        {
          name: 'drinkProducts.drinkList[1].milk',
          type: 'tea',
          img: drink2,
          id: '002',
        },
        {
          name: 'drinkProducts.drinkList[2].almondTea',
          type: 'tea',
          img: drink3,
          id: '003',
        },
        {
          name: 'drinkProducts.drinkList[3].lemonTea',
          type: 'tea',
          img: drink1,
          id: '004',
        },
        {
          name: 'drinkProducts.drinkList[4].espressoRomano',
          type: 'coffee',
          img: drink4,
          id: '005',
        },
        {
          name: 'drinkProducts.drinkList[5].americano',
          type: 'coffee',
          img: drink5,
          id: '006',
        },
        {
          name: 'drinkProducts.drinkList[6].latte',
          type: 'coffee',
          img: drink1,
          id: '007',
        },
      ],
    };
  },
  methods: {
    changProduct(e = 0, id = '001') {
      const imgList = Object.values(document.getElementsByClassName('drinkList'));
      this.productList.forEach((item) => {
        if (item.id === id) {
          this.productId = id;
        }
      });
      imgList.forEach((item) => {
        // eslint-disable-next-line
        item.style.opacity = 0;
      });
      imgList[e].style.opacity = 1;
    },
  },
  mounted() {
    this.changProduct();
  },
};
</script>
