<template>
  <section class="d-flex flex-column h-100">
    <h2 class="fw-bolder mb-3">{{ $t('cakeProducts.title') }}</h2>
    <div class="mb-3 imgSet d-flex justify-content-center">
      <template v-for="(item, index) in productList" :key="index">
        <img
          :src="item.img"
          class="img-fluid cakeList"
          alt=""
          style="width: 75%"
        />
      </template>
    </div>
    <div class="mb-5">
      <ul class="h4">
        <li class="fw-bold mb-2">{{ $t('cakeProducts.classification') }}</li>
        <ul class="fw-lighter text-dark-emphasis ambienceSet">
          <template v-for="(item, index) in productList" :key="index">
            <li class="mb-1" :class="{focusLine: index !== productNum}">
              <a
                href="#"
                class="d-inline fw-lighter text-dark-emphasis text-decoration-none"
                @click.prevent
                @mouseover="changProduct(index)"
                @focus="bar"
              >
                {{ $t(item.name) }}
              </a>
            </li>
          </template>
        </ul>
      </ul>
    </div>
    <div class="text-end mt-auto">
      <small class="text-body-secondary">Full report on page 7</small>
    </div>
  </section>
</template>

<script>
import cake1 from '@/assets/images/cake-01.png';
import cake2 from '@/assets/images/cake-02.png';
import cake3 from '@/assets/images/cake-03.png';

export default {
  data() {
    return {
      productNum: 0,
      productList: [
        {
          name: 'cakeProducts.cakeList[0].applePie',
          img: cake1,
        },
        {
          name: 'cakeProducts.cakeList[1].brownies',
          img: cake2,
        },
        { name: 'cakeProducts.cakeList[2].financier', img: cake3 },
        { name: 'cakeProducts.cakeList[3].lemonCake', img: cake2 },
      ],
    };
  },
  methods: {
    changProduct(e) {
      const imgList = Object.values(document.getElementsByClassName('cakeList'));
      this.productNum = e;
      imgList.forEach((item) => {
        // eslint-disable-next-line
        item.style.opacity = 0;
      });
      imgList[e].style.opacity = 1;
    },
  },
  mounted() {
    this.changProduct(0);
  },
};
</script>
